import React from 'react';

const footer = (props) => {
    return (
        <footer className="py-2 bg-dark">
          <div className="container">
          <p className="m-0 text-center text-white">Copyright &copy; Arsalan 2018</p>
          </div>
        </footer>
    )
}

export default footer;


